<template>
    <div class="box_bg">
            <div class="box">
                <div class="header">
                    <a class="active" href="javascript:;">注册</a>
                    <a href="javascript:;">登录</a>
                </div>
                <div class="body">
                    <div class="con">
                        <form action="/user/register" method="POST" @submit.prevent="registerSubmit">
                            <input type="text" placeholder="手机号/昵称：" name="username" v-model="regData.username" />
                            <input type="password" placeholder="密 码：" name="password" v-model="regData.password" />
                            <input type="password" placeholder="再次输入密码：" name="repassword" v-model="regData.repassword" />
                            <button type="submit">确认注册</button>
                            <p class="message">{{message}}</p>
                        </form>
                    </div>
                </div>
            </div>
        </div>
</template>
<script>
import '@/assets/css/register_login.css';
import axios from 'axios';

export default {
    data(){
        return {
            regData:{},
            message:''
        }
    },
    methods:{
        async registerSubmit(){
            let res = await axios({
                method:'post',
                url:'/api/user/register',
                data:{
                    username:this.regData.username,
                    password:this.regData.password,
                    repassword:this.regData.repassword
                }
            });
            if(res){
                 this.message = res.data.message;
                 if(!res.data.code){
                     //登录成功 
                     //表单输入框内容为空
                     this.regData.username = '';
                     this.regData.password = '';
                     this.regData.repassword = '';
                     //停留1000毫秒 自动跳转到注册页面
                     setTimeout(()=>{
                        //  window.location.href = '/login';
                         this.$router.push('/login');
                     },1000);
                    
                }
             }
        }
    }
}
</script>